.top-menu__item {
  padding: 36px 0;
  position: relative;
  transition: all 0.3s ease-in; }
  .top-menu__item:hover .top-menu__link::after {
    opacity: 1; }

.top-menu__item,
.dropdown-menu__item {
  margin-right: 5rem; }

@media (max-width: 1399px) {
  .header-page__content {
    display: none !important;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    height: 100vh;
    background-color: #fff;
    z-index: 35;
    right: 0;
    top: 0;
    bottom: 0;
    transform: translate3d(300px, 0, 0);
    transition: all 0.6s ease-in-out; } }

.header-page__content.open {
  transform: translateZ(0);
  overflow: auto;
  opacity: 1;
  visibility: visible;
  display: block !important; }

.top-menu__link::after {
  content: "";
  position: absolute;
  background: #2275c2;
  height: 3px;
  width: 100%;
  left: 0;
  bottom: 0;
  opacity: 0;
  transition: all 0.3s ease-in; }

.top-menu__link,
.dropdown-menu__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  display: block;
  transition: all 0.3s ease;
  text-decoration: none;
