.hero-page {
    background: linear-gradient(92.95deg, #1B2E58 0.13%, #019AB9 100%);
    box-shadow: 0px 10px 20px -5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    margin-top: 5rem;
    color: #fff;

    @include respond(tabdesk) {
      flex-direction: column;
      margin-top: 26px;
    }

    & .col-12:first-child {
        padding: 50px;

        @include respond(tabdesk) {
          padding: 20px 10px;
          justify-content: center;
          display: flex;
          flex-direction: column;
          align-items: center;
        }
    }
    & .col-12:last-child {
        background: url(../../images/img/hero-img.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 10px;
        width: 50%;

        @include respond(tabdesk) {
          width: 100%;
          height: 100%;
          min-height: 300px;
        }
    }

    & .title {
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
        text-transform: uppercase;
        margin-bottom: 44px;

        @include respond(tabdesk) {
          margin-bottom: 20px;
          font-size: 18px;
          line-height: 22px;
        }
    }

    & .description {
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        display: block;
        margin-bottom: 38px;
        max-width: 53rem;

        @include respond(tabdesk) {
          font-size: 14px;
          margin-bottom: 20px;
        }
    }

    & .hero-page__btns {
        
        @include respond(mobilemini) {
          width: 100%;
          flex-direction: column;
          align-items: center;
        }

        @include respond(mobilemini) {
            & .global-btn:first-child {
                margin-bottom: 20px;
            }
        }
    }

    & .global-btn {
        background-color: transparent;
        border: 1px solid #fff;

        @include respond(mobilemini) {
          width: 100%;
          margin-right: 0;
        }

        &:hover {
            color: #293B62;
            background: #FFFFFF;
        }
    }
}